import React, { Component } from 'react' 
import PropTypes from 'prop-types'
import Usage from '../../Usage'
import BooleansField from '../../common/BooleansField'


class Exts extends Component {
    constructor(props){
        super(props)
        this.update = this.update.bind(this)
    }
    
    update() {
        this.props.updateGraphicsMethod()
    }

    render(){
        return (
            <div>
                <label>Extensions</label>
                <BooleansField 
                    inline
                    updatedValue={this.update}
                    labels={["Extend before first value", "Extend after last value"]}
                    options={["ext_1", "ext_2"]}
                    value={{"ext_1": this.props.ext1, "ext_2": this.props.ext2}}
                />
            </div>
        );
    }
}

Exts.propTypes = { 
    updateGraphicsMethod: PropTypes.func,
    ext1: PropTypes.bool,
    ext2: PropTypes.bool,
}

export default Exts;
